import * as Yup from "yup";
import FieldEnum from "../../../enums/FieldEnum";
import alphabetRegex from "../froms.regex";

const validationSchema = Yup.object().shape({
    [FieldEnum.Name]: Yup.string()
        .trim()
        .matches(alphabetRegex, "Name cannot include a numbers or another special symbols")
        .required("Name is required"),
    [FieldEnum.Email]: Yup.string().email().required("Email is required"),
    [FieldEnum.Password]: Yup.string()
        .trim()
        .min(5, "Password must be at least 5 characters")
        .required("Password is required"),
});

export default validationSchema;
